body {
  font-family: 'Poppins', sans-serif;
    color: white;
  margin: 0px;
  padding: 0px;
  background: url(bg.jpg);
  background-repeat: no-repeat;
  background-size:120%;
  display: block;
}

button{
font-family: 'Poppins', sans-serif;
padding: 6px 6px;

margin: auto -68px;
font-size: 1.2rem;
border-radius: 10px;
cursor: pointer;
font-weight: bold;
color: black;
background-color: white;
border: 2px solid black;
}
button:hover{
/* color: black;
background-color: white;
border: 2px solid black; */
border: 2px solid white;
background-color: pink;
color:white;
}

.column1{
height: 100%;
float: left;
width: 90%;
}
.column2{
height: 100%;
float: left;
width: 10%;
}

.row{
display: flex;
align-items: center;
position: sticky;
top: 0px;
}

.row::before{
content: "";
background-color: black;
position: absolute;
top: 0px;
left: 0px;
z-index: -1;
width: 100%;
height: 100%;
opacity: 0.4;
}

.box2{
margin-left: 100px;
}

.avatar{
border-radius: 50%;
}


.column3{
float: left !important;
width: 33% !important;
}
h2{
margin: 13px 43px;
padding-top: 9px 10px;
}

.profile {
display: inline-block;
text-align: center;
font-family: arial;
border: 2px solid;
margin: 20px auto;
padding: 20px 37px;
border-radius: 13px;
}

.profile:hover{
transition:400ms;
box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
transform: translateY(-3px);
border-color: red;
background-color: pink;
}
.email {
color: rgb(59, 52, 52);
font-size: 18px;
overflow-wrap: anywhere;
}
.name{
font-size: 26px;
font-weight: bold;
color: #190000;
}
.loader{
margin-left: 300px;
margin-top: 100px;
margin-right: 80px;
}
.content {
flex: 1 1 40rem;
padding: 3rem;
}

footer{
background-color: black;
color: white;
padding: 9px 20px;
margin: 3px 0px;
display: grid;
text-align: center;
}